<template>
	<div class="calendarium">
		<button class="add-manual" @click="adding_manually = true">
			<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-plus">
				<line x1="12" y1="5" x2="12" y2="19"></line>
				<line x1="5" y1="12" x2="19" y2="12"></line>
			</svg>
		</button>
		<div class="manual-add" v-show="adding_manually">
			<form name="manual-form">
				<label>Название
					<el-input v-model="manual_form['title']" name="title" placeholder="Название" />
				</label>
				<label>Описание
					<el-input v-model="manual_form['description']" type="textarea" name="description" placeholder="Описание" rows="2"></el-input>
				</label>
				<label>Дата
					<el-date-picker v-model="manual_form['date']" type="date" placeholder="Дата" value-format="yyyy-MM-dd">
					</el-date-picker>
				</label>
				<label>
					Начало
					<el-time-select :picker-options="{ start: '00:00',step: '00:20',end: '23:00'}" v-model="manual_form['from']" placeholder="Начало">
					</el-time-select>
				</label>
				<label>
					Конец
					<el-time-select :picker-options="{ start: '00:00',step: '00:20',end: '23:00'}" v-model="manual_form['to']" placeholder="Конец">
					</el-time-select>
				</label>

				<button type="button" @click="addManually()" class="save">Добавить</button>
				<button type="button" class="close" @click="adding_manually = false">
					<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-plus">
						<line x1="12" y1="5" x2="12" y2="19"></line>
						<line x1="5" y1="12" x2="19" y2="12"></line>
					</svg>
				</button>
			</form>
		</div>
		<div class="calendar-component" :class="{'show-slots': outline_slots}">
			<kalendar :configuration="calendar_settings" :appointments="appointments" class="generate-shadow">
				<div slot="creating-card" slot-scope="{appointment_props}">
					<h4 class="appointment-title" style="text-align: left;">Новое событие</h4>
					<span class="time">{{getHours(appointment_props.start_value.value, appointment_props.end_value.value)}}
					</span>
				</div>
				<div slot="popup-form" slot-scope="{popup_scope}" style="display: flex; flex-direction: column;">
					<h4 style="margin-bottom: 10px">Новое событие</h4>
					<input v-model="new_appointment['title']" type="text" name="title" placeholder="Заголовок">
					<textarea v-model="new_appointment['description']" type="text" name="description" placeholder="Описание" rows="2"></textarea>
					<div class="buttons">
						<button class="cancel" @click="popup_scope.close_popup = true">Закрыть</button>
						<button @click="completeAppointment(popup_scope, new_appointment)">Сохранить</button>
					</div>
				</div>
				<div slot="details-card" slot-scope="{appointment_props}" class="details-card">
					<h4 class="appointment-title">{{appointment_props.data.title}}</h4>
					<small v-show="(appointment_props.end - appointment_props.start) > 2">{{appointment_props.data.description}}</small>
					<span class="time">{{appointment_props.start_value.value | normalizeDate('hh:mm HH')}} - {{appointment_props.end_value.value | normalizeDate('hh:mm A')}}</span>
					<button @click="removeAppointment(appointment_props)" class="cancel">
						<svg class="feather feather-x-circle sc-dnqmqq jxshSx" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" aria-hidden="true" data-reactid="1326"><circle cx="12" cy="12" r="10"></circle><line x1="15" y1="9" x2="9" y2="15"></line><line x1="9" y1="9" x2="15" y2="15"></line></svg>
					</button>
				</div>
				<template slot="first-date" slot-scope="{ date }">
					{{ date | normalizeDate('MMM DD') }}
				</template>
				<template slot="last-date" slot-scope="{ date }">
					{{ date | normalizeDate('DD MMM, YYYY') }}
				</template>
				<template slot="current-date" slot-scope="{ date }">
					{{ date | normalizeDate('DD MMM, YYYY') }}
				</template>
				<template slot="number-date" slot-scope="{ date }">
					{{ date | normalizeDate('D') }}
				</template>
				<template slot="letters-date" slot-scope="{ date }">
					{{ date | normalizeDate('ddd') }}
				</template>
			</kalendar>
		</div>
	</div>
</template>
<script>
const existing_appointments = [];
import Vue from 'vue';

import VueHighlightJS from 'vue-highlightjs'
Vue.use(VueHighlightJS)

import format from 'date-fns/format';
import getTime from 'date-fns/get_time';

import Kalendar from '../components/kalendar/kalendar-container.vue';
//import { Kalendar } from 'kalendar-vue';
//import 'kalendar-vue/dist/kalendar-vue.css';

import { DatePicker, TimePicker, Select, Input, TimeSelect, Option, Checkbox } from 'element-ui';
import lang from 'element-ui/lib/locale/lang/ru-RU';
import locale from 'element-ui/lib/locale';
locale.use(lang);


let new_appointment_model = {
	description: null,
	title: null,
};

let manual_appointment_model = { ...new_appointment_model,
	from: null,
	to: null,
	date: null,
};

export default {
	components: {
		Kalendar,
		elInput: Input,
		elSelect: Select,
		elTimePicker: TimePicker,
		elDatePicker: DatePicker,
		elTimeSelect: TimeSelect,
		elCheckbox: Checkbox,
	},
	data: () => ({
		appointments: existing_appointments,
		calendar_settings: {
			style: 'material_design',
			view_type: 'Month',
			split_value: 20,
			cell_height: 20,
			scrollToNow: false,
			current_day: new Date(),
			military_time: false,
			read_only: false
		},
		outline_slots: false,
		new_appointment: JSON.parse(JSON.stringify(new_appointment_model)),
		manual_form: JSON.parse(JSON.stringify(manual_appointment_model)),
		adding_manually: false,
	}),
	methods: {
		getHours(start, end) {
			return `${format(start, 'hh:mm HH')} - ${format(end, 'hh:mm HH')}`;
		},
		completeAppointment(popup_data, form_data) {
			let payload = {
				data: {
					title: form_data.title,
					description: form_data.description,
				},
				from: popup_data.appointment_props.start_value.value,
				to: popup_data.appointment_props.end_value.value,
				date: format(popup_data.appointment_props.start_value.value, 'YYYY-MM-DD'),
			};
			this.appointments.push(payload);
			this.new_appointment = JSON.parse(JSON.stringify(new_appointment_model));
			popup_data.close_popup = true;
		},
		addManually() {
			let { title, description, from, to, date } = this.manual_form;

			from = `${date} ${from}:00`;
			to = `${date} ${to}:00`;
			let payload = {
				data: { title, description },
				from,
				to,
				date: format(from, 'YYYY-MM-DD'),
			};

			this.appointments.push(payload);
			this.manual_form = JSON.parse(JSON.stringify(manual_appointment_model));
			this.adding_manually = false;
		},
		removeAppointment(appointment) {
			let index = this.appointments.findIndex(item => item.from === appointment.start_value.value);
			this.appointments.splice(index, 1);
		},
	},
};
</script>
